import { AppBar, Avatar, Badge, Box, InputBase, Menu, MenuItem, styled, Toolbar, Typography } from '@mui/material'
import Pets from '@mui/icons-material/Pets';
import React, { useState } from 'react'
import { Mail, Notifications } from '@mui/icons-material';

const StyledToolbar=styled(Toolbar)({
    display:"flex",
    justifyContent:"space-between"
})

const Search=styled("div")(({theme})=>(
    {backgroundColor:"white",
    padding:"0px 4px 0px",
    borderRadius:theme.shape.borderRadius,
    width:"40%"
}))

const Icons=styled(Box)(({theme})=>(
    {
        display:"none",   //smaller than 600 then show none else flex
        gap:"20px",
        alignItems:"center",
        [theme.breakpoints.up("sm")]:{
            display:"flex"
        }
}))

const UserBox=styled(Box)(({theme})=>(
    {
        display:"flex",        //smaller than 600 then none
        gap:"10px",
        alignItems:"center",
        [theme.breakpoints.up("sm")]:{
            display:"none"   // bigger than 600 then none
        }
}))

export const Navbar = () => {
    const[open, setOpen] =useState(false)
  return (

    <AppBar position='sticky'>
        <StyledToolbar>
            <Typography variant="h6" sx={{display:{xs:"none",sm:"block"}}}>
            Rahul Dev
            </Typography>
            <Pets sx={{display:{xs:"block",sm:"none"}}}/>
            <Search><InputBase placeholder='Search...' /> </Search>
            <Icons>
            <Badge badgeContent={5} color="error">
                <Mail/>
            </Badge>
            <Badge badgeContent={2} color="error">
                <Notifications/>
            </Badge>
            <Avatar src="" sx={{width:27,height:27,}}
            onClick={e=>setOpen(true)}/>
            </Icons>
            <UserBox onClick={e=>setOpen(true)}>
            <Avatar src="" sx={{width:27,height:27,}}
            />
            <Typography variant="span">Rahul</Typography>
            </UserBox>
        </StyledToolbar>
        <Menu
        id="demo-positioned-menu"
        aria-labelledby="demo-positioned-button"
        open={open}
        onClose={e=>setOpen(false)}
        
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        <MenuItem >Profile</MenuItem>
        <MenuItem >My account</MenuItem>
        <MenuItem >Logout</MenuItem>
      </Menu>
    </AppBar>
  )
}
 